import { InputGroup, Form } from 'react-bootstrap';

export default function SignupMajor({ snum, check }) {
	const majorCheck = (snum) => {
		if (check.snum == 'hidden') {
			if (snum.slice(4, 7) == 136) return '';
			else if (snum.slice(4, 7) == 123) return '';
			else if (snum.slice(4, 7) == 120) return '';
			else if (snum.slice(4, 7) == 128) return '';
		} else return '';
	};
	return (
		<InputGroup className='mb-3'>
			<InputGroup.Text id='basic-addon1'>Major</InputGroup.Text>
			<Form.Control
				aria-label='Major'
				aria-describedby='basic-addon1'
				disabled
				value={majorCheck(snum)}
			/>
		</InputGroup>
	);
}
